import { globalOptions } from '../shared/ajax.js';
import { extend } from './extend.js';
/**
 *  ajax 
 * @param options 
 * @example
```js
ajaxSetup({
  dataType: 'json',
  method: 'POST',
});
```
 */
// eslint-disable-next-line @typescript-eslint/no-explicit-any
export const ajaxSetup = (options) => {
    return extend(globalOptions, options);
};
