/**
 *  HTML  true
 *  attr="false"  false
 *
 * 
 *  vue3  :attr="value" vue  DOM  attr 
 *  DOM  attribute 
 *  vue ssr DOM  attribute  attr="true"  attr="false"
 *  attribute  attr="false"  false
 *
 *  custom-elements.json 
 * 
 *
 * @see https://v3-migration.vuejs.org/zh/breaking-changes/attribute-coercion.html
 */
export const booleanConverter = (value) => {
    return value !== null && value !== 'false';
};
