import { argbFromHex } from '@material/material-color-utilities';
import { setFromSource } from '../internal/colorScheme.js';
/**
 * 
 * @param hex  #f82506
 * @param options
 * @param options.target  CSS DOM  JQ  `<html>` 
 * @param options.customColors 
 * @param options.customColors[].name 
 * @param options.customColors[].value  #f82506
 */
export const setColorScheme = (hex, options) => {
    const source = argbFromHex(hex);
    setFromSource(source, options);
};
