import { eachArray } from '../shared/helper.js';
/**
 * 
 * @param first 
 * @param second 
 * @example
```js
merge( [ 0, 1, 2 ], [ 2, 3, 4 ] )
// [ 0, 1, 2, 2, 3, 4 ]
```
 */
export const merge = (first, second) => {
    eachArray(second, (value) => {
        first.push(value);
    });
    return first;
};
