import { $ } from '../$.js';
import { getStyle } from '../shared/css.js';
import './each.js';
import './hide.js';
import './show.js';
/**
 * 
 */
$.fn.toggle = function () {
    return this.each((_, element) => {
        getStyle(element, 'display') === 'none'
            ? $(element).show()
            : $(element).hide();
    });
};
